    --primary-color: #264653;
    --accent-color: #2A9D8F;
    --background-color: #F9FAFB;
    --header-color: #264653;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #111827;
    height: 100vh;
    background-color: var(--background-color);
}

.header {
    background-color: var(--header-color);
    padding: 1rem;
    display: flex;
    align-items: center;
    height: 12%;
    box-shadow: 2px 2px 5px lightgray;
    position: relative;
}

.logo {
    margin-right: auto;
}

.header > button {
    background-color: var(--accent-color);
    transition: background-color 0.2s ease;
    padding: 0.5em;
    border-radius: 0.5rem;
    color: #F9FAFB;
    cursor: pointer;
    font-size: 1.5rem;
    border: none;
}

.header > button:hover {
    background-color: #21867A;
}

.modal,
.project-modal {
    position: fixed;
    z-index: 1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border: none;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 2px 2px 5px gray;
}

.modal-close-btn,
.remove-project-btn,
.project-modal-close-btn {
    border: none;
    padding: 0.05em 0.3em;
    position: absolute;
    right: 5%;
    top: 10%;
    cursor: pointer;
    background-color: #F87171;
    transition: background-color 0.2s ease;
}

.modal-close-btn:hover,
.remove-project-btn:hover {
    background-color: #EF4444;
}

.modal > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}

.modal > form > h2 {
    margin-bottom: 0.5rem;
}

.modal > form > div {
    width: 100%;
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 0.5rem 1rem;
}

.modal > form input,
.modal > form textarea {
    padding: 0.3rem;
}

.modal > form input:user-invalid,
.modal > form textarea:user-invalid {
    border: 2px dashed red;
}

#project-description {
    resize: none;
}

.modal > form fieldset {
    grid-column: 1 / 3;

}

.modal > form fieldset > legend {
    margin-bottom: 0.5rem;
}

.create-project-btn {
    padding: 0.5em 1em;
    border-radius: 0.5rem;
    border: none;
    background-color: var(--accent-color);
    color: white;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.create-project-btn:hover {
    background-color: #21867A;
}

.title {
    text-align: center;
    margin: 2rem auto;
    font-size: 3rem;
}

.projects {
    height: 88%;
    background-color: inherit;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-auto-rows: 300px;
    padding: 2rem;
    gap: 2rem;
}

.project-card {
    border: 3px solid lightgray;
    padding: 1rem;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: background-color 0.2s ease;
}

.project-title {
    align-self: center;
    margin-bottom: 1rem;
}

.project-due-date {
    position: absolute;
    bottom: 10px;
    right: 10px;
    font-size: 0.8rem;
    opacity: 0.5;
}

.low-priority {
    background-color: #FEF9C3;
    color: #92400E;
}

.low-priority:hover {
    background-color: #FDE68A;
}

.medium-priority {
    background-color: #FDE68A;
    color: #B45309;
}

.medium-priority:hover {
    background-color: #FCD34D;
}

.high-priority {
    background-color: #FCA5A5;
    color: #7F1D1D;
}

.high-priority:hover {
    background-color: #FB8C8C;
}

.change-priority-popup {
    padding: 1rem;
    position: absolute;
    background-color: white;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.change-priority-popup > fieldset > div {
    display: flex;
    gap: 0.3rem;
}

.change-priority-popup button {
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    cursor: pointer;
    background-color: #E0E0E0;
    transition: background-color 0.2s ease;
}

.change-priority-popup button:hover {
    background-color: #CCCCCC;
}

.project-modal {
    height: 60%;
    width: clamp(500px, 80vw, 800px);
}

.modal-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-modal-heading {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.project-modal-heading-change > input {
    font-size: 3rem;
    text-align: center;
}

.project-modal-description {
    opacity: 0.5;
    margin-bottom: 2rem;
}

.project-modal-description-change > textarea {
    resize: none;
    padding: 0.5rem;
}

.project-modal-duedate,
.project-modal-duedate-change {
    position: fixed;
    top: 20px;
    left: 20px;
    font-size: 0.8rem;
    opacity: 0.5;
}

.project-modal-heading-change,
.project-modal-description-change,
.project-modal-duedate-change {
    display: flex;
    gap: 1rem;
}

.project-modal-heading-change > button,
.project-modal-description-change > button,
.project-modal-duedate-change > button {
    border: none;
    background-color: transparent;
}

.task-list {
    list-style-type: none;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    justify-content: center;
    align-items: start;
}

.task-list > li {
    display: flex;
    gap: 0.8rem;
    width: 100%;
}

.task-list input[type="text"] {
    padding: 0.2rem;
}

.task-list input[type="text"]:focus {
    width: 50%;
}

.confirm-task-btn,
.delete-task-btn {
    padding: 0.1rem;
    border: none;
    background-color: transparent;
    font-size: 1rem;
}

.add-task-button {
    background-color: var(--accent-color);
    transition: background-color 0.2s ease;
    padding: 0.5em;
    border-radius: 0.5rem;
    color: #F9FAFB;
    cursor: pointer;
    font-size: 0.8rem;
    border: none;
    margin-top: 1rem;
}

.add-task-button:hover {
    background-color: #21867A;
}

.add-task-button:disabled {
    opacity: 0.5;
}

.checked-task {
    text-decoration: line-through;
    opacity: 0.5;
}`, "",{"version":3,"sources":["webpack://./src/css/styles.css"],"names":[],"mappings":"AAAA;IACI,wBAAwB;IACxB,uBAAuB;IACvB,2BAA2B;IAC3B,uBAAuB;AAC3B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,mJAAmJ;IACnJ,cAAc;IACd,aAAa;IACb,yCAAyC;AAC7C;;AAEA;IACI,qCAAqC;IACrC,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,iCAAiC;IACjC,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,qCAAqC;IACrC,sCAAsC;IACtC,cAAc;IACd,qBAAqB;IACrB,cAAc;IACd,eAAe;IACf,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;;IAEI,eAAe;IACf,UAAU;IACV,SAAS;IACT,QAAQ;IACR,gCAAgC;IAChC,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,4BAA4B;AAChC;;AAEA;;;IAGI,YAAY;IACZ,qBAAqB;IACrB,kBAAkB;IAClB,SAAS;IACT,QAAQ;IACR,eAAe;IACf,yBAAyB;IACzB,sCAAsC;AAC1C;;AAEA;;IAEI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,sCAAsC;IACtC,gBAAgB;AACpB;;AAEA;;IAEI,eAAe;AACnB;;AAEA;;IAEI,sBAAsB;AAC1B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;;AAEtB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,qBAAqB;IACrB,YAAY;IACZ,qCAAqC;IACrC,YAAY;IACZ,eAAe;IACf,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,yBAAyB;IACzB,aAAa;IACb,4DAA4D;IAC5D,qBAAqB;IACrB,aAAa;IACb,SAAS;AACb;;AAEA;IACI,2BAA2B;IAC3B,aAAa;IACb,qBAAqB;IACrB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,sCAAsC;AAC1C;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,qBAAqB;IACrB,eAAe;IACf,yBAAyB;IACzB,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,kBA